<h1 id="vue-js-简介" tabindex="-1"><a class="header-anchor" href="#vue-js-简介" aria-hidden="true">#</a> Vue.js 简介</h1>
<h2 id="什么是vue-js" tabindex="-1"><a class="header-anchor" href="#什么是vue-js" aria-hidden="true">#</a> 什么是Vue.js</h2>
<p>响应式，前后端分离
双向数据绑定</p>
<p>!&gt; 不支持<code>IE8</code>及其以下版本，因为使用了<code>ECMAScript 5</code>的特点</p>
<p>Vue.js是用来开发<code>web</code>界面的前端库。</p>
<p>特点
具有响应式编程和组件化的特点</p>
<p>Vue.js的组件化理念，一切都是组件，如何理解这句话。可以将任意封装好的代码注册成<code>标签</code></p>
<h2 id="为什么要使用vue-js" tabindex="-1"><a class="header-anchor" href="#为什么要使用vue-js" aria-hidden="true">#</a> 为什么要使用Vue.js</h2>
<p>MVVM的开发模式，不需要在维护视图和数据的统一上花大量的时间，只需要关注data的变化，代码更加可维护</p>
<p>理想状态下，在前后端分离的项目中使用vue.js最合适</p>
